@import '../variables';

.categories {
  ul {
    display: flex;
    flex-wrap: wrap;
    @media (max-width: 1260px) {
      gap: 20px;
    }

    li {
      background-color: #f9f9f9;
      padding: 13px 30px;
      border-radius: 30px;
      margin-right: 10px;
      font-weight: bold;
      cursor: pointer;
      transition: background-color 0.1s ease-in-out;
      @include noselect();
      @media (max-width: 1260px) {
        width: 100%;
        margin-right: 0;
        text-align: center;
      }

      &:hover {
        background-color: darken(#f9f9f9, 2%);
      }

      &:active {
        background-color: darken(#f9f9f9, 5%);
      }

      &.active {
        background-color: #282828;
        color: #fff;
      }
    }
  }
}
