@import '../variables';

.header {
  border-bottom: 1px solid $gray-line;
  padding: 40px 0;
  @media (max-width: 820px) {
    padding: 20px 0;
  }

  .container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 30px;
    flex-wrap: wrap;
    @media (max-width: 810px) {
      flex-direction: column;

    }
  }
  &__cart {
    @media (max-width: 820px) {
      width: 100%;
    }
  }
  &__logo {
    display: flex;

    img {
      margin-right: 15px;
    }

    h1 {
      color: #181818;
      font-size: 24px;
      letter-spacing: 1px;
      text-transform: uppercase;
      font-weight: 800;
    }

    p {
      color: #7b7b7b;
      @media (max-width: 440px) {
        font-size: 16px;
      }
    }

  }
}
.button--cart {
  @media (max-width: 820px) {
    justify-content: center;
  }

}
